








































































































































































































import uniSegmentedControl from '../../../components/uni-segmented-control.vue';
import uniNumberBox from '../../../components/uni-number-box.vue'
export default {
	components: {
		uniSegmentedControl,uniNumberBox
	},
	data() {
		const currentDate = this.getDate({
			format: true
		});
		return {
			items: [
				'本金还款',
				//'空卡垫资',
				'精养卡',
			],
			current: 0,
			activeColor: '#fe5950',
			styleType: 'text',
			credit_id: '',
			card: [],
			loading: false,
			start_time:currentDate,
			end_time: currentDate,
			repay_num:1,
			repay_money:'',
			pay_num:10,
			period_num:1,
			pay_money:500,
			bannerShow:false,
			pay_date_show:false,
			pay_date:[],
		}
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
	onLoad(e) {
		if (e.credit_id) {
			this.credit_id = e.credit_id;
		} else {
			uni.switchTab({
				url: '/pages/tabBar/card/card'
			})
		}
	},
	onShow() {
		this.getCreditCardInfo();
	},
	onBackPress() {},
	methods: {
		onNumberChange(value) {
			this.pay_num = value;
		},
		onNumberChange2(value) {
			this.period_num = value;
		},
		onNumberChange3(value) {
			this.pay_money = value;
		},
		closeBanner: function() {
			this.bannerShow = false;
		},
		getTipZzj(){
			this.bannerShow = true;
		},
		getRepayStep: function () {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const repay_money = this.repay_money;
			const current = this.current;
			const credit_id = this.credit_id;
			const repay_num = this.repay_num;
			const start_time = this.start_time;
			const end_time = this.end_time;
			
			const pay_money = this.pay_money;
			const pay_num = this.pay_num;
			const period_num = this.period_num;
			const pay_date = this.pay_date;
			if(current==2){
				if(pay_money==''){
					uni.showModal({
						content: '请输入每笔最大消费金额',
						showCancel: false
					});
					return;
				}
				//console.log(start_time.replace(/-/g,""));
				if(start_time.replace(/-/g,"") > end_time.replace(/-/g,"")){
					uni.showModal({
						content: '结束日期不能大于开始日期',
						showCancel: false
					});
					return;
				}
				
			}else{
				if(repay_money==''){
					uni.showModal({
						content: '请输入还款金额',
						showCancel: false
					});
					return;
				}
				//console.log(start_time.replace(/-/g,""));
				if(start_time.replace(/-/g,"") > end_time.replace(/-/g,"")){
					uni.showModal({
						content: '还款结束日期不能大于还款开始日期',
						showCancel: false
					});
					return;
				}
			}
			const that = this;
			try {
				if(this.loading){
					return;
				}
				this.loading = true;
				
				this.$http.post(this.websiteUrl + '/api/repay/getRepayStep', {
					openid,
					sessionKey,
					repay_money,
					current,
					credit_id,
					repay_num,
					start_time,
					end_time,
					pay_money,
					period_num,
					pay_num,
					pay_date,
				}, (res) => {
						if(res.data.code==1){
							that.loading = false;
							uni.navigateTo({
								url: '/pages/card/credit/repayStep?credit_id='+credit_id+'&temp_id='+res.data.result
							})
						}else{
							uni.showModal({
								content:res.data.msg,
								showCancel:false,
								success:function() {
									that.loading = false;
								}
							})
						}						
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false,
					success:function() {
						that.loading = false;
					}
				});
			}
		},
		switch1Change: function (e) {
			this.getDateAll(this.start_time,this.end_time);
			this.pay_date_show = e.target.value;
		},
		onClickPayDay(index){
			var items = this.pay_date;
				if(items[index].checked){
					items[index].checked = false;
				}else{
					items[index].checked = true;
				}
			this.pay_date = items;
		},
		onClickNum(num){
			this.repay_num = num;
		},
		bindDateChange: function(e) {
			this.start_time = e.target.value;
			this.getDateAll(this.start_time,this.end_time);
		},
		bindDateChange_end: function(e) {
			this.end_time = e.target.value;
			this.getDateAll(this.start_time,this.end_time);
		},
		getDateAll(begin, end) {  
		   let ab = begin.split("-");  
		   let ae = end.split("-");  
		   let db = new Date();  
		   db.setUTCFullYear(ab[0], ab[1] - 1, ab[2]);  
		   let de = new Date();  
		   de.setUTCFullYear(ae[0], ae[1] - 1, ae[2]);  
		   let unixDb = db.getTime();  
		   let unixDe = de.getTime();  
		   var items = [];
		   var date = '';
		   var i = 0;
		   for (let k = unixDb; k <= unixDe;) {  
			   date = new Date(parseInt(k));
				items[i]={
					value:this.getNewDate(date),
					checked:true,
					day:date.getDate(),
				};
				i = i + 1;
			   k = k + 24 * 60 * 60 * 1000;  
		   } 
		   this.pay_date = items;
	   },
	   getNewDate(date) {
	   	let year = date.getFullYear();
	   	let month = date.getMonth() + 1;
	   	let day = date.getDate();
	   	month = month > 9 ? month : '0' + month;
	   	day = day > 9 ? day : '0' + day;
	   	//console.log(year, month, day)
	   	return `${year}-${month}-${day}`;
	   },
		getDate(type) {
			const date = new Date();

			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 1;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;
			day = day > 9 ? day : '0' + day;
			//console.log(year, month, day)
			return `${year}-${month}-${day}`;
		},
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		},
		getCreditCardInfo() {
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			const credit_id = this.credit_id;
			try {
				this.$http.post(this.websiteUrl + '/api/card/getCreditCardInfo', {
					openid,
					sessionKey,
					credit_id
				}, (res) => {
					if (res.data.code == 1) {
						this.card = res.data.result;
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content: '网络异常,请稍后重试...',
					showCancel: false
				})
			}
		},
	}
}
